import React from 'react';
import ReviewsData from '@/assets/data/reviews/data.json';
import classes from './Reviewscard.module.scss';
import { getImage, getImageWebp } from '@/utils/icons';

const Reviewscard = () => {
  return (
    <>
      {ReviewsData.map((item, index) => {
        const pngSrc = getImage(item.img);       // fallback PNG/JPG/GIF
        const webpSrc = getImageWebp(item.img);  // WebP, если есть

        return (
          <div key={index} className={classes.card}>
            <div className={classes.content}>
              <h2>{item.headline}</h2>
              <p>{item.content}</p>
            </div>
            <div className={classes.author}>
              <div className={classes.author_left}>
                {pngSrc && (
                  <picture>
                    {webpSrc && <source srcSet={webpSrc} type="image/webp" />}
                    <img src={pngSrc} alt={item.name} />
                  </picture>
                )}
                <div className={classes.author_info}>
                  <h5>{item.name}</h5>
                  <span>{item.position}</span>
                </div>
              </div>
              <a href={item.href} target="_blank" rel="noopener noreferrer">
                Open Website
              </a>
            </div>
          </div>
        );
      })}
    </>
  );
};

export default Reviewscard;
